.force-directed {
  position: relative;
  width: 1000px;
  margin: 0 auto; 
  background-color: white;
  -webkit-box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.32), 0 3px 12px 0 rgba(0, 0, 0, 0.24);
          box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.32), 0 3px 12px 0 rgba(0, 0, 0, 0.24);

}

.force-directed .links line {
  stroke: #aaa;
}
.force-directed .flag-box {
  position: absolute;
  left: 0;
  top: 0; 
}

.force-directed .flag {
  position: absolute;

}

.force-directed .tooltip {
  width: auto;
  z-index: 10;
  text-align: center;
  background: #757575;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}



